/* eslint-disable no-unused-vars */
/* eslint-disable import/no-cycle */
/* eslint-disable no-restricted-globals */
import { takeLatest, call, put, all } from 'redux-saga/effects';

import { toast } from 'react-toastify';
import api from '../../../services/api';

import bucket from '../../index';

import { getTeamSuccess, getTeamFailure } from './actions';
import { attUserTeam } from '../user/actions';

export function* getTeam() {
  const { team } = bucket.store.getState().user.user;

  if (team && team.teamId) {
    const { teamId } = team;

    try {
      const response = yield call(api.get, `team/${teamId}`);

      yield put(getTeamSuccess({ team: response.data }));
    } catch (err) {
      yield put(getTeamFailure());
    }
  } else {
    yield put(getTeamFailure());
  }
}

export function* outTeam() {
  try {
    const response = yield call(api.post, 'team/quit');
    yield put(attUserTeam());
  } catch (error) {
    toast.error(error.response.data.error);
    yield put(attUserTeam());
  }
}

export default all([
  takeLatest('@team/GET_TEAM_REQUEST', getTeam),
  takeLatest('@team/OUT_TEAM_REQUEST', outTeam)
]);
