import React from 'react';
import { Switch } from 'react-router-dom';
import Route from './Route';

import SignIn from '../pages/SignIn';
import SignUp from '../pages/SignUp';
import Tasks from '../pages/Tasks';
import SingleTaskPage from '../pages/SingleTaskPage';
import Team from '../pages/Team';
import Support from '../pages/Support';

export default function Routes() {
  return (
    <Switch>
      <Route path="/" exact component={SignIn} />
      <Route path="/signUp" component={SignUp} />

      <Route path="/tasks" exact isPrivate component={Tasks} />
      <Route path="/tasks/:id" isPrivate component={SingleTaskPage} />
      <Route path="/team" isPrivate component={Team} />
      <Route path="/support" isPrivate component={Support} />
      <Route path="/" isPrivate component={Team} />
    </Switch>
  );
}
