/* eslint-disable react/prop-types */
/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Grid, Paper, withStyles, Typography } from '@material-ui/core';
import LoadingComponent from '../../components/LoadingComponent';
import TaskList from '../../components/TaskList';
import TaskListWithoutTeam from '../../components/TaskListWithoutTeam';
// import { Container } from './styles';

import { getTeam } from '../../store/modules/team/actions';

import styles from './styles';

function Tasks({ classes }) {
  const dispatch = useDispatch();
  const user = useSelector(state => state.user.user);
  const team = useSelector(state => state.team.team);
  const loadingUser = useSelector(state => state.user.loading);
  const loadingTeam = useSelector(state => state.team.loading);

  useEffect(() => {
    if (!team && !loadingUser) {
      dispatch(getTeam());
    }
  }, [user]);

  return (
    <Paper className={classes.root}>
      <Grid container spacing={2}>
        <Grid item xs={12}>
          {loadingTeam || loadingUser ? (
            <LoadingComponent />
          ) : (
            <>
              {team ? (
                <Typography
                  variant="h5"
                  className={classes.text}
                  component="h3"
                >
                  Equipe {team.name}
                </Typography>
              ) : (
                <Typography
                  variant="h5"
                  className={classes.text}
                  component="h3"
                >
                  Sem Equipe
                </Typography>
              )}
            </>
          )}
        </Grid>

        {loadingTeam || loadingUser ? null : (
          <Grid item xs={12}>
            {team ? <TaskList /> : <TaskListWithoutTeam />}
          </Grid>
        )}
      </Grid>
    </Paper>
  );
}

export default withStyles(styles)(Tasks);
