/* eslint-disable no-unused-vars */
/* eslint-disable react/prop-types */
import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { toast } from "react-toastify";
import {
  withStyles,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
  Grid,
  Button,
  RadioGroup,
  Radio,
  FormControlLabel
} from "@material-ui/core/";
import LoadingComponent from "../../../../LoadingComponent";

import api from "../../../../../services/api";
import { getTeam } from "../../../../../store/modules/team/actions";
import { attUserTeam } from "../../../../../store/modules/user/actions";

import styles from "./styles";

function ModalChangeLevel({ classes, open, setOpen, user }) {
  const dispatch = useDispatch();
  const [loading, setLoading] = useState(false);
  const [newLevel, setNewLevel] = useState("cordenador");
  const teamId = useSelector(state => state.team.team.teamId);

  async function handleAtt() {
    setLoading(true);
    try {
      await api.put(`user/level/${teamId}`, {
        ...user,
        newLevel
      });

      dispatch(getTeam());
      setOpen(false);
    } catch (error) {
      // TEM QUE TERMINAR DE PEGAR O USUARIO ANTES DE DAR getTean();
      dispatch(attUserTeam());

      toast.error(error.response.data.error);
    }

    setLoading(false);
  }

  return (
    <Dialog
      open={open}
      keepMounted
      aria-labelledby="alert-dialog-slide-title"
      aria-describedby="alert-dialog-slide-description"
    >
      <DialogTitle id="alert-dialog-slide-title">
        Mudar {user.name} de funo na equipe?
      </DialogTitle>
      <DialogContent>
        <DialogContentText id="alert-dialog-slide-description">
          Escolha a nova funo desse participante.
        </DialogContentText>

        <RadioGroup
          aria-label="Type"
          name="Type"
          value={newLevel}
          onChange={e => setNewLevel(e.target.value)}
          className={classes.radios}
        >
          <FormControlLabel
            value="coordenador"
            control={<Radio color="primary" />}
            label="Coordenador"
          />
          <FormControlLabel
            value="participante"
            control={<Radio color="primary" />}
            label="Participante"
          />
        </RadioGroup>
      </DialogContent>
      <DialogActions>
        {loading ? (
          <Grid item xs={12} className={classes.loading}>
            <LoadingComponent />
          </Grid>
        ) : (
          <>
            <Button onClick={() => setOpen(!open)} color="primary">
              Cancelar
            </Button>
            <Button onClick={() => handleAtt()} color="primary">
              Confirmar
            </Button>
          </>
        )}
      </DialogActions>
    </Dialog>
  );
}

export default withStyles(styles)(ModalChangeLevel);
