/* eslint-disable import/no-cycle */
import { all, call, put, takeLatest } from 'redux-saga/effects';

import api from '../../../services/api';

import { getUserSuccess, attUserSucces } from './actions';
import { signOut } from '../auth/actions';

import bucket from '../../index';
import history from '../../../services/history';

export function* getUser() {
  const { signed } = bucket.store.getState().auth;

  if (signed) {
    try {
      const response = yield call(api.get, `user`);

      yield put(getUserSuccess(response.data));
    } catch (error) {
      yield history.push('/');
      yield put(signOut());
    }
  }
}

export function* attUserTeam() {
  const { signed } = bucket.store.getState().auth;

  if (signed) {
    try {
      const response = yield call(api.get, '/user/with-team');

      const { user, team } = response.data;

      yield put(attUserSucces({ user, team }));
      // yield put(getTeam());
    } catch (error) {
      yield history.push('/');
      yield put(signOut());
      yield history.push('/');
    }
  }
}

export default all([
  takeLatest('@user/GET_USER_REQUEST', getUser),
  takeLatest('@user/ATT_USER_TEAM', attUserTeam)
]);
