/* eslint-disable no-unused-vars */
/* eslint-disable react/prop-types */
import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { toast } from 'react-toastify';
import {
  withStyles,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
  Button,
  Grid
} from '@material-ui/core/';

import LoadingComponent from '../../../../LoadingComponent';

import styles from './styles';

import { getTeam } from '../../../../../store/modules/team/actions';
import { attUserTeam } from '../../../../../store/modules/user/actions';

import api from '../../../../../services/api';

function ModalExclueMember({ classes, open, setOPen, user }) {
  const dispatch = useDispatch();
  const teamId = useSelector(state => state.team.team.teamId);
  const [loading, setLoading] = useState(false);

  async function handleExclue() {
    setLoading(true);
    try {
      await api.post('/team/quit-force', {
        userId: user.userId,
        teamId
      });

      dispatch(getTeam());
      setOPen(false);
    } catch (error) {
      // TEM QUE TERMINAR DE PEGAR O USUARIO ANTES DE DAR getTean();
      dispatch(attUserTeam());

      toast.error(error.response.data.error);
    }

    setLoading(false);
  }

  return (
    <Dialog
      open={open}
      keepMounted
      aria-labelledby="alert-dialog-slide-title"
      aria-describedby="alert-dialog-slide-description"
    >
      <DialogTitle id="alert-dialog-slide-title">
        Excluir {user.name} da equipe ?
      </DialogTitle>
      <DialogContent>
        <DialogContentText id="alert-dialog-slide-description">
          O participante pode entrar na equipe novamente mais tarde.
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        {loading ? (
          <Grid item xs={12} className={classes.loading}>
            <LoadingComponent />
          </Grid>
        ) : (
          <>
            <Button onClick={() => setOPen(!open)} color="primary">
              Cancelar
            </Button>
            <Button onClick={() => handleExclue()} color="primary">
              Excluir
            </Button>
          </>
        )}
      </DialogActions>
    </Dialog>
  );
}

export default withStyles(styles)(ModalExclueMember);
