export function getUser() {
  return {
    type: '@user/GET_USER_REQUEST'
  };
}

export function getUserSuccess(user) {
  return {
    type: '@user/GET_USER_SUCCESS',
    payload: { user }
  };
}

export function attUserTeam() {
  return {
    type: '@user/ATT_USER_TEAM'
  };
}

export function attUserSucces({ user, team }) {
  return {
    type: '@user/ATT_USER_TEAM_SUCCESS',
    payload: { user, team }
  };
}
