/* eslint-disable react/prop-types */
import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Paper, withStyles, Typography, Button } from '@material-ui/core/';
import LoadingComponent from '../LoadingComponent';

import { outTeam } from '../../store/modules/team/actions';

import styles from './styles';

function PersonalInfo({ classes, user, loading }) {
  const loadingOut = useSelector(state => state.team.loadingOut);
  const loadingTeam = useSelector(state => state.team.loading);
  const dispatch = useDispatch();

  const team = useSelector(state => state.team.team);

  async function handleOutTeam() {
    dispatch(outTeam());
  }

  return (
    <>
      {loading ? (
        <Paper className={classes.root}>
          <LoadingComponent />
        </Paper>
      ) : (
        <Paper className={classes.root}>
          <Typography variant="h5" className={classes.text} component="h3">
            Informaes pessoais
          </Typography>

          <Typography color="textPrimary" variant="subtitle1">
             Nome: {user.name}
          </Typography>
          <Typography color="textSecondary" variant="subtitle1">
             Registro: {user.register}
          </Typography>
          <Typography color="textSecondary" variant="subtitle1">
             Sesso: {user.session}
          </Typography>
          <Typography color="textSecondary" variant="subtitle1">
             Unidade Escoteira: {user.groupName} - {user.groupNumber}
          </Typography>
          <Typography color="textSecondary" variant="subtitle1">
             Regios: {user.region}
          </Typography>

          {user.team && (user.team.type || user.team.level) ? (
            <>
              {user.team.level ? (
                <Typography color="textSecondary" variant="subtitle1">
                   Nivel: {user.team.level}
                </Typography>
              ) : (
                <Typography color="textSecondary" variant="subtitle1">
                  {user.team.type}
                </Typography>
              )}
            </>
          ) : (
            <Typography color="textSecondary" variant="subtitle1">
               Nivel: Sem Equipe
            </Typography>
          )}

          {team && (
            <Button
              variant="contained"
              size="large"
              color="primary"
              className={classes.button}
              onClick={() => handleOutTeam()}
              disabled={loadingOut || loadingTeam}
            >
              Sair da Equipe
            </Button>
          )}
        </Paper>
      )}
    </>
  );
}

export default withStyles(styles)(PersonalInfo);
