import React from 'react';
import {
  Grid,
  Paper,
  Typography,
  withStyles,
  Divider
} from '@material-ui/core';
import LinesEllipsisLoose from 'react-lines-ellipsis/lib/loose';

import styles from './styles';
// import { Container } from './styles';

function TaskComponent({ classes, task }) {
  function handleClick() {
    console.log(task);
  }

  return (
    <Grid item xs={12} md={4} onClick={() => handleClick()}>
      <Paper
        className={classes.root}
        style={
          task.answer
            ? task.answer.status === 'Corrigida'
              ? { background: '#ed5565' }
              : task.answer.status === 'Enviado para correao'
              ? { background: `#1ab394` }
              : { background: `#23c6c8` }
            : { background: `#f8ac59` }
        }
      >
        <Typography variant="h5" ccomponent="h3" className={classes.number}>
          {task.task.number}
        </Typography>

        <Divider orientation="vertical" className={classes.divider} />

        <LinesEllipsisLoose
          text={task.task.title}
          className={classes.name}
          maxLine="1"
          lineHeight="16"
        />

        {task.answer && task.answer.status === 'Corrigida' ? (
          <>
            <Divider orientation="vertical" className={classes.divider} />

            <Typography
              variant="h5"
              ccomponent="h3"
              className={classes.percent}
            >
              {task.answer.grade}%
            </Typography>
          </>
        ) : null}
      </Paper>
    </Grid>
  );
}

export default withStyles(styles)(TaskComponent);
