/* eslint-disable import/no-cycle */
import { all, call, put, takeLatest } from 'redux-saga/effects';
import { toast } from 'react-toastify';

import api from '../../../services/apiTasks';

import { getTasksSuccess, getTasksFailure } from './actions';

import bucket from '../../index';
// import history from '../../../services/history';

export function* getTasks() {
  const { team } = bucket.store.getState().team;

  if (team) {
    try {
      const response = yield call(
        api.get,
        `tasks/${team.teamId}?type=${team.type}`
      );

      console.log(response.data);

      const tasks = response.data;

      yield put(getTasksSuccess({ tasks }));
    } catch (error) {
      toast.error(error.response.data.error);
      yield put(getTasksFailure());
    }
  } else
    try {
      const response = yield call(api.get, `tasks/1?type=Geral`);

      console.log(response.data);

      const tasks = response.data;

      yield put(getTasksSuccess({ tasks }));
    } catch (error) {
      toast.error(error.response.data.error);
      yield put(getTasksFailure());
    }
}

export default all([takeLatest('@tasks/GET_TEAM_TASKS_REQUEST', getTasks)]);
