import { produce } from 'immer';

const INITIAL_STATE = {
  tasks: [],
  loading: false
};

export default function auth(state = INITIAL_STATE, action) {
  return produce(state, draft => {
    switch (action.type) {
      case '@tasks/GET_TEAM_TASKS_REQUEST': {
        draft.loading = true;
        break;
      }

      case '@tasks/GET_TEAM_TASKS_SUCCESS': {
        draft.tasks = action.payload.tasks;
        draft.loading = false;
        break;
      }

      case '@tasks/GET_TEAM_TASKS_FAILURE': {
        draft.loading = false;
        break;
      }

      default:
    }
  });
}
