/* eslint-disable no-unused-vars */
/* eslint-disable react/prop-types */
import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Grid,
  withStyles,
  TextField
} from "@material-ui/core";
import { toast } from "react-toastify";
import LoadingComponent from "../../LoadingComponent";

import api from "../../../services/api";

import { getUser, attUserTeam } from "../../../store/modules/user/actions";

import styles from "./styles";

function ModalJoinTeam({ classes, open, setOpen }) {
  const dispatch = useDispatch();
  const [loading, setLoading] = useState(false);
  const [teamId, setTeamId] = useState("");
  const [error, setError] = useState(false);

  function handleClose() {
    setOpen(false);
  }

  async function handleJoinTeam() {
    if (teamId !== "") {
      setError(false);
      setLoading(true);
      try {
        await api.post("team/join", { teamId });

        dispatch(getUser());
        handleClose();
      } catch (errors) {
        toast.error(errors.response.data.error);
        handleClose();
        dispatch(attUserTeam());
      }
      setLoading(false);
    } else {
      setError(true);
    }
  }

  return (
    <div>
      <Dialog
        open={open}
        onEnter={() => {
          setTeamId("");
          setError(false);
        }}
        aria-labelledby="Modal join team"
        aria-describedby="Modal join team"
      >
        <DialogTitle id="alert-dialog-slide-title">
          Entrar em uma equipe por codigo
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-slide-description">
            Digite o codigo da equipe que deseja entrar no campo abaixo.
          </DialogContentText>

          <TextField
            id="codigo"
            label="Codigo da equipe"
            value={teamId}
            className={classes.input}
            fullWidth
            required
            error={error}
            onChange={e => setTeamId(e.target.value)}
          />
        </DialogContent>

        {loading ? (
          <Grid item xs={12} className={classes.loading}>
            <LoadingComponent />
          </Grid>
        ) : (
          <DialogActions>
            <Button onClick={() => handleClose()} color="primary">
              Cancelar
            </Button>

            <Button onClick={() => handleJoinTeam()} color="primary">
              Entrar
            </Button>
          </DialogActions>
        )}
      </Dialog>
    </div>
  );
}

export default withStyles(styles)(ModalJoinTeam);
