export function getTeam() {
  return {
    type: '@team/GET_TEAM_REQUEST'
  };
}

export function getTeamSuccess({ team }) {
  return {
    type: '@team/GET_TEAM_SUCCESS',
    payload: { team }
  };
}

export function getTeamFailure() {
  return {
    type: '@team/GET_TEAM_FAILURE'
  };
}

export function outTeam() {
  return {
    type: '@team/OUT_TEAM_REQUEST'
  };
}

export function outTeamSuccess() {
  return {
    type: '@team/OUT_TEAM_REQUEST_SUCCESS'
  };
}
