export function getTasksRequest() {
  return {
    type: '@tasks/GET_TEAM_TASKS_REQUEST'
  };
}

export function getTasksSuccess({ tasks }) {
  return {
    type: '@tasks/GET_TEAM_TASKS_SUCCESS',
    payload: { tasks }
  };
}

export function getTasksFailure() {
  return {
    type: '@tasks/GET_TEAM_TASKS_FAILURE'
  };
}
