/* eslint-disable no-use-before-define */
import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Grid, Typography } from '@material-ui/core';

import LoadingComponent from '../LoadingComponent';
import TaskComponent from './TaskComponent';

import api from '../../services/apiTasks';

import { getTasksSuccess } from '../../store/modules/Tasks/actions';

export default function TaskList({ classes }) {
  const dispatch = useDispatch();
  const tasks = useSelector(state => state.tasks.tasks);

  const [loading, setLoading] = useState(false);
  const team = useSelector(state => state.team.team);

  useEffect(() => {
    async function getTasks() {
      const response = await api.get(`tasks/${team.teamId}?type=${team.type}`);

      const tasks = response.data;

      dispatch(getTasksSuccess({ tasks }));
    }

    if (tasks.length === 0) {
      setLoading(true);
      getTasks();
      setLoading(false);
    }
  }, [dispatch, tasks, team.teamId, team.type]);

  return (
    <Grid container spacing={2}>
      <Grid item xs={12}>
        <h1> tarefas</h1>
      </Grid>

      <Grid container spacing={2} style={{ marginBottom: 15 }}>
        <Grid item xs={12}>
          <h1>Status :</h1>
        </Grid>
        <Grid item xs={12} style={{ display: 'flex', alignItems: 'center' }}>
          <div
            style={{
              background: 'rgb(248, 172, 89)',
              height: 25,
              width: 25,
              borderRadius: 4,
              marginRight: 10
            }}
          />
          <Typography variant="h5" ccomponent="h3">
            Aberta
          </Typography>
        </Grid>

        <Grid item xs={12} style={{ display: 'flex', alignItems: 'center' }}>
          <div
            style={{
              background: '#1ab394',
              height: 25,
              width: 25,
              borderRadius: 4,
              marginRight: 10
            }}
          />
          <Typography variant="h5" ccomponent="h3">
            Corrigindo
          </Typography>
        </Grid>
        <Grid item xs={12} style={{ display: 'flex', alignItems: 'center' }}>
          <div
            style={{
              background: '#23c6c8',
              height: 25,
              width: 25,
              borderRadius: 4,
              marginRight: 10
            }}
          />
          <Typography variant="h5" ccomponent="h3">
            Recorrecao
          </Typography>
        </Grid>
        <Grid item xs={12} style={{ display: 'flex', alignItems: 'center' }}>
          <div
            style={{
              background: '#ed5565',
              height: 25,
              width: 25,
              borderRadius: 4,
              marginRight: 10
            }}
          />
          <Typography variant="h5" ccomponent="h3">
            Corrigida
          </Typography>
        </Grid>
      </Grid>

      {loading ? (
        <>
          <LoadingComponent />
        </>
      ) : (
        <Grid container spacing={2}>
          {tasks.map((task, index) => (
            <TaskComponent task={task} key={index} />
          ))}
        </Grid>
      )}
    </Grid>
  );
}
