/* eslint-disable no-unused-vars */
/* eslint-disable react/prop-types */
import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  TextField,
  withStyles,
  RadioGroup,
  FormControlLabel,
  Radio,
  Grid,
  CircularProgress
} from "@material-ui/core";
import { toast } from "react-toastify";

import api from "../../../services/api";

import styles from "./styles";
import { getUser, attUserTeam } from "../../../store/modules/user/actions";
import LoadingComponent from "../../LoadingComponent";

function ModalCreateTeam({ classes, open, setOpen }) {
  const dispatch = useDispatch();
  const [teamName, setTeamName] = useState("");
  const [description, setDescription] = useState("");
  const [loading, setLoading] = useState(false);
  const user = useSelector(state => state.user.user);

  const [type, setType] = useState("Geral");

  function handleClose() {
    setOpen(false);
  }

  async function handleSubmit(e) {
    e.preventDefault();

    setLoading(true);

    try {
      await api.post("team/create", {
        teamName,
        description,
        type,
        groupName: user.groupName,
        groupNumber: user.groupNumber,
        session: user.session,
        name: user.name,
        date: user.date
      });

      dispatch(getUser());
      handleClose();
    } catch (error) {
      toast.error(error.response.data.error);
      handleClose();
      dispatch(attUserTeam());
    }

    setLoading(false);

    // dispatch(createTeam({ name, description, type }));
  }

  return (
    <div>
      <Dialog
        open={open}
        aria-labelledby="Modal join team"
        aria-describedby="Modal join team"
      >
        <DialogTitle id="alert-dialog-slide-title">
          Criar uma equipe
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-slide-description">
            Preencha os dados da sua nova equipe!
          </DialogContentText>
        </DialogContent>

        <DialogContent>
          <form onSubmit={e => handleSubmit(e)}>
            <TextField
              value={teamName}
              onChange={e => setTeamName(e.target.value)}
              required
              margin="dense"
              id="name"
              label="Nome da sua equipe"
              type="text"
              fullWidth
            />
            <TextField
              value={description}
              onChange={e => setDescription(e.target.value)}
              required
              margin="dense"
              id="description"
              label="Descrio da sua equipe"
              type="text"
              fullWidth
            />

            <RadioGroup
              aria-label="Type"
              name="Type"
              value={type}
              row
              onChange={e => setType(e.target.value)}
              className={classes.radios}
            >
              <FormControlLabel
                value="Geral"
                control={<Radio color="primary" />}
                label="Geral"
              />

              <FormControlLabel
                value="Lobinho"
                control={<Radio color="primary" />}
                label="Lobinho"
              />
            </RadioGroup>

            {loading ? (
              <Grid item xs={12} className={classes.loading}>
                <LoadingComponent />
              </Grid>
            ) : (
              <DialogActions className={classes.actions}>
                <Button onClick={() => handleClose()} color="primary">
                  Cancelar
                </Button>
                <Button type="submit" color="primary">
                  Criar
                </Button>
              </DialogActions>
            )}
          </form>
        </DialogContent>
      </Dialog>
    </div>
  );
}

export default withStyles(styles)(ModalCreateTeam);
