/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable react/prop-types */
import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Grid, withStyles } from "@material-ui/core";

import TeamComponent from "../../components/TeamComponent";
import PersonalInfo from "../../components/PersonalInfo";
import TeamMembers from "../../components/TeamMembers";

import { getTeam } from "../../store/modules/team/actions";
import styles from "./styles";

function Team({ classes }) {
  const dispatch = useDispatch();
  const user = useSelector(state => state.user.user);
  const loadingUser = useSelector(state => state.user.loading);
  const team = useSelector(state => state.team.team);
  const loadingTeam = useSelector(state => state.team.loading);

  useEffect(() => {
    if (!team && !loadingUser) {
      dispatch(getTeam());
    }
  }, [user]);

  return (
    <Grid container spacing={2}>
      <Grid item xs={12} md={6}>
        <TeamComponent team={team} loading={!!(loadingUser || loadingTeam)} />
      </Grid>
      <Grid item xs={12} md={6}>
        <PersonalInfo user={user} loading={loadingUser} />
      </Grid>
      <Grid item xs={12} className={classes.TeamMembers}>
        <TeamMembers team={team} loading={!!(loadingUser || loadingTeam)} />
      </Grid>
    </Grid>
  );
}

export default withStyles(styles)(Team);
