/* eslint-disable react/prop-types */
import React, { useState } from 'react';
import { withStyles, Menu, MenuItem } from '@material-ui/core';

import styles from './styles';

import ModalChangeLevel from '../ModalChangeLevel';
import ModalExclueMember from '../ModalExclueMember';

function ModalMamberOptions({ classes, setOpen, user, anchorEl }) {
  const [exclue, setExclue] = useState(false);
  const [changelvl, setChangelvl] = useState(false);

  function handleClose() {
    setOpen(false);
  }

  function handleSubmit(e) {
    e.preventDefault();
  }

  return (
    <div>
      <Menu
        open={Boolean(anchorEl)}
        onClose={() => handleClose(null)}
        anchorEl={anchorEl}
        placement="right"
      >
        <MenuItem
          onClick={() => {
            handleClose();
            setExclue(!exclue);
          }}
        >
          Excluir da equipe
        </MenuItem>
        <MenuItem
          onClick={() => {
            handleClose();
            setChangelvl(!changelvl);
          }}
        >
          Mudar funo
        </MenuItem>
      </Menu>

      <ModalExclueMember open={exclue} setOPen={setExclue} user={user} />
      <ModalChangeLevel open={changelvl} setOpen={setChangelvl} user={user} />
    </div>
  );
}

export default withStyles(styles)(ModalMamberOptions);
