/* eslint-disable react/prop-types */
import React from 'react';

import { Paper, withStyles, Typography } from '@material-ui/core/';
import styles from './styles';
import LoadingComponent from '../LoadingComponent';

import TeamMembersList from './TeamMembersList';

function TeamMembers({ classes, team, loading }) {
  return (
    <>
      {loading ? (
        <Paper className={classes.root}>
          <LoadingComponent />
        </Paper>
      ) : (
        <Paper className={classes.root}>
          {team ? (
            <TeamMembersList users={team.users} />
          ) : (
            <>
              <Typography
                variant="h5"
                className={classes.text}
                component="h3"
                align="center"
              >
                Voc ainda no tem nenhuma equipe
              </Typography>
              <Typography
                color="textSecondary"
                variant="subtitle1"
                align="center"
              >
                Crie ou se junte a uma equipe e comece essa aventura!
              </Typography>
            </>
          )}
        </Paper>
      )}
    </>
  );
}

export default withStyles(styles)(TeamMembers);
