import { produce } from 'immer';

const INITIAL_STATE = {
  team: null,
  loading: false,
  loadingOut: false
};

export default function team(state = INITIAL_STATE, action) {
  return produce(state, draft => {
    switch (action.type) {
      case '@team/GET_TEAM_REQUEST': {
        draft.loading = true;
        break;
      }

      case '@team/GET_TEAM_SUCCESS': {
        draft.team = action.payload.team;
        draft.loading = false;
        break;
      }

      case '@team/GET_TEAM_FAILURE': {
        draft.loading = false;
        break;
      }

      case '@auth/SIGN_OUT_REQUEST': {
        draft.team = null;
        break;
      }

      case '@team/OUT_TEAM_REQUEST': {
        draft.loading = true;
        draft.loadingOut = true;
        break;
      }

      case `@user/ATT_USER_TEAM`: {
        draft.loading = true;
        break;
      }

      case `@user/ATT_USER_TEAM_SUCCESS`: {
        draft.team = action.payload.team;
        draft.loading = false;
        draft.loadingOut = false;
        break;
      }

      default:
    }
  });
}
